<template>
  <div>
    <h1>Team Unified Comparer</h1>
    <div class="d-flex">
      <div class="deck-container">
        <input class="deckname1" placeholder="Deck 1">
        <textarea class="deck1" name="Deck1" cols="40" rows="25" placeholder="4 Scalding Tarn..."></textarea>
      </div>
      <div class="deck-container">
        <input class="deckname2" placeholder="Deck 2">
        <textarea class="deck2" name="Deck2" cols="40" rows="25" placeholder="4 Misty Rainforest..."></textarea>
      </div>
      <div class="deck-container">
        <input class="deckname3" placeholder="Deck 3">
        <textarea class="deck3" name="Deck3" cols="40" rows="25" placeholder="4 Marsh Flats..."></textarea>
      </div>
    </div>
    <button class="compare" v-on:click="compare">Compare</button>
    <ul class="result"></ul>
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {

  },
  methods: {
    compare: function () {
      const deck1 = document.documentElement.querySelector('.deck1');
      const deck2 = document.documentElement.querySelector('.deck2');
      const deck3 = document.documentElement.querySelector('.deck3');

      const deck1Array = deck1.value.split(/\n/);
      const deck2Array = deck2.value.split(/\n/);
      const deck3Array = deck3.value.split(/\n/);

      const deckNames = ['Deck 1', 'Deck 2', 'Deck 3'];
      const deckname1 = document.documentElement.querySelector('.deckname1');
      const deckname2 = document.documentElement.querySelector('.deckname2');
      const deckname3 = document.documentElement.querySelector('.deckname3');

      if (deckname1.value) {
        deckNames[0] = deckname1.value;
      }

      if (deckname2.value) {
        deckNames[1] = deckname2.value;
      }

      if (deckname3.value) {
        deckNames[2] = deckname3.value;
      }


      const map = new Map();

      [deck1Array, deck2Array, deck3Array].forEach((array, deckIndex) => {
        array.forEach((line) => {
          const card = line.substring(line.indexOf(' ') + 1);
          const amount = parseInt(line.substring(0, line.indexOf(' ')));

          if (amount) {
            if (map.has(card)) {
              map.set(card, {
                'amount': map.get(card).amount + amount,
                'in': map.get(card).in += ', ' + deckNames[deckIndex],
              });
            } else {
              map.set(card, {
                'amount': amount,
                'in': deckNames[deckIndex],
              });
            }
          }
        });
      })

      let output = '';
      const basics = ['plains', 'island', 'swamp', 'mountain', 'forest', 'snow-covered plains', 'snow-covered island', 'snow-covered swamp', 'snow-covered mountain', 'forest'];

      map.forEach((content, card) => {
        if (content.amount > 4 && !basics.includes(card.toLowerCase())) {
          output += `<li>${card} is used ${content.amount} times. (${content.in})</li>`;
        }
      });

      if (output === '') {
        output = '<li class="valid">Decks are valid for Team Unified.</li>'
      }

      const result = document.documentElement.querySelector('.result');
      result.innerHTML = output;
    }
  }
}
</script>

